:root {
  --color-blue-lagoon: #007c89;
  --color-bokara-grey: #241c15;
  --color-opaltone: #692340;
  --color-double-pearl-lusta: #fbeeca;
  --color-grey: #8c8c8c;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.page {
  height: 100%;
  font-family: "Roboto", sans-serif;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  color: var(--color-opaltone);
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.logo {
  width: 186px;
  height: 100%;
}
.logo__image {
  width: 100%;
}

.main-title {
  font-size: 4.625rem;
}

.section-title {
  font-size: 3.375rem;
}

.description {
  font-size: 1.25rem;
}

.text {
  font-size: 1rem;
}

.prescription {
  font-size: 0.875rem;
}

.header__container {
  display: grid;
  column-gap: 10px;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  margin: 0 auto;
  padding: 20px 15px;
  min-height: 45px;
  max-width: 1380px;
}
.header__auth {
  display: grid;
  column-gap: 12px;
  justify-self: end;
  grid-template-columns: auto 1fr;
}
.header__logo {
  display: flex;
  align-items: center;
  justify-self: center;
}

.nav__list {
  display: grid;
  column-gap: 10px;
  grid-template-columns: repeat(4, 1fr);
}

.auth__log-btn {
  border: 1px solid #000;
  padding: 11px 29px;
  color: var(--color-bokara-grey);
}
.auth__sign-btn {
  padding: 11px 16px;
  color: #fff;
  background-color: var(--color-blue-lagoon);
}

.hero {
  min-height: 764px;
  background-image: url(../../img/hero__background.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1180px;
}
.hero__text-wrapper {
  padding-top: 100px;
  max-width: 640px;
}
.hero__main-title {
  margin: 0 0 38px;
  font-weight: 500;
  line-height: 74px;
}
.hero__description {
  margin: 0 0 38px;
  line-height: 28px;
}
.hero__btn {
  margin: 0 0 20px;
  padding: 20px 40px;
  font-size: 0.9375rem;
  line-height: 20px;
  color: var(--color-double-pearl-lusta);
  background-color: var(--color-opaltone);
}
.hero__prescription {
  margin: 0;
  padding-bottom: 20px;
  line-height: 21px;
}

.benefits {
  padding-top: 128px;
}
.benefits__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1180px;
}
.benefits__text-wrapper {
  margin: 0 0 120px;
  max-width: 690px;
  text-align: center;
}
.benefits__list {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
}
.benefits__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 287.5px;
}
.benefits__item-title {
  margin: 0 0 20px;
}
.benefits__item-description {
  margin: 0;
  max-width: 200px;
  text-align: center;
}
.benefits__image {
  margin: 0 0 31px;
}

.users {
  padding-top: 120px;
}
.users__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1030px;
}
.users__text-wrapper {
  padding: 0 10px;
  max-width: 528px;
  text-align: center;
}
.users__title {
  margin: 0 0 18px;
  font-weight: 400;
  font-size: 2.5rem;
}
.users__title-modify {
  font-weight: 400;
  font-size: 7rem;
  text-transform: uppercase;
}
.users__image {
  min-width: 270px;
}

.path {
  padding-top: 116px;
}
.path__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1070px;
}
.path__text-wrapper {
  margin-bottom: 100px;
  max-width: 660px;
  text-align: center;
}
.path__section-title {
  margin: 0 0 37px;
}
.path__list {
  width: 100%;
}
.path__subtitle {
  margin: 0 0 22px;
  font-size: 2.5rem;
}
.path__text {
  margin: 0 0 13px;
}
.path__text_margin {
  margin: 0 0 16px 15px;
}
.path__link {
  color: var(--color-opaltone);
}
.path__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.path__item:not(:last-child) {
  margin-bottom: 134px;
}
.path__item-text-wrapper {
  margin-right: 15px;
  margin-bottom: 20px;
  padding-left: 83px;
  max-width: 605px;
}
.path__item-image {
  min-width: 300px;
}
.path__item-logo {
  margin: 0 0 43px;
}

.reviews {
  margin-bottom: 72px;
  padding-top: 72px;
}
.reviews__container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1180px;
}
.reviews__section-title {
  text-align: center;
}

.swiper {
  padding: 0 50px;
  height: 725px;
  max-width: 1035px;
}
.swiper-container {
  position: relative;
  padding: 0 55px;
}
.swiper-button-prev {
  left: 0;
  color: var(--color-opaltone);
}
.swiper-button-next {
  right: 0;
  color: var(--color-opaltone);
}
.swiper-pagination {
  position: absolute;
  left: 0;
  bottom: 0;
}
.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.swiper__text {
  max-width: 350px;
  font-size: 40px;
  line-height: 44px;
}
.swiper__text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 10px;
  padding-bottom: 30px;
  height: 100%;
}
.swiper-pagination-bullet-active {
  background-color: var(--color-opaltone);
}
.swiper__image {
  margin-right: 20px;
  width: 456px;
}

.join-section {
  min-height: 600px;
  background-color: var(--color-double-pearl-lusta);
}
.join-section__container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1180px;
}
.join-section__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 863px;
}
.join-section__title {
  margin: 116px 0 37px;
  font-weight: 700;
  font-size: 3.1875rem;
  line-height: 54px;
  text-align: center;
}
.join-section__description {
  margin: 0 0 27px;
  text-align: center;
}
.join-section__btn {
  margin: 0 0 27px;
  border: 1px solid #692340;
  padding: 20px 40px;
  font-weight: 700;
  font-size: 0.9375rem;
  color: var(--color-opaltone);
}

.inspiration {
  padding-bottom: 120px;
}
.inspiration__container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1240px;
}
.inspiration__section-title {
  text-align: center;
}
.inspiration__btn {
  border: 1px solid #692340;
  padding: 20px 40px;
}
.inspiration-buttons {
  display: flex;
  justify-content: space-evenly;
  margin: 0 auto 20px;
  max-width: 1035px;
}
.inspiration-buttons__title {
  font-size: 1.5rem;
  line-height: 26px;
  text-align: center;
}
.inspiration-buttons__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 217px;
}

.business-tips {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
}
.business-tips__item {
  margin-bottom: 20px;
  max-width: 385px;
}
.business-tips__category {
  margin: 0;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 55px;
}
.business-tips__title {
  margin: 0 0 13px;
  font-weight: 700;
  font-size: 1.6875rem;
  line-height: 30px;
}
.business-tips__description {
  margin: 0 0 20px;
  font-size: 1.125rem;
  line-height: 29px;
}
.business-tips__image {
  margin: 0 0 8px;
}

.start-section {
  display: flex;
  align-items: center;
  min-height: 577px;
  background-color: var(--color-opaltone);
}
.start-section__container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1180px;
}
.start-section__text-wrapper {
  max-width: 585px;
  text-align: center;
  color: var(--color-double-pearl-lusta);
}
.start-section__title {
  margin: 0 0 35px;
  font-weight: 400;
  font-size: 4.625rem;
  line-height: 74px;
  letter-spacing: 1px;
}
.start-section__description {
  margin: 0 0 30px;
  line-height: 28px;
}
.start-section__btn {
  margin: 0 0 20px;
  padding: 20px 40px;
  color: var(--color-opaltone);
  background-color: var(--color-double-pearl-lusta);
}
.start-section__prescription {
  margin: 0 0 20px;
  line-height: 21px;
}

.footer {
  padding: 121px 0;
  min-height: 875px;
  color: var(--color-bokara-grey);
  background-color: #efeeea;
}
.footer__container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1070px;
}
.footer__top {
  display: grid;
  grid-template-columns: minmax(auto, 810px) 185px;
  margin: 0 0 130px;
  gap: 60px;
}
.footer__category-list {
  display: grid;
  column-gap: 60px;
  grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
}
.footer__links-list {
  margin-bottom: 20px;
}
.footer__links-link {
  display: flex;
  flex-direction: column;
  font-size: 13px;
  color: var(--color-bokara-grey);
}
.footer__links-item:not(:last-child) {
  margin: 0 0 13px;
}
.footer__services-item {
  display: flex;
  flex-direction: column;
}
.footer__services-item:not(:last-child) {
  margin: 0 0 70px;
}
.footer__services-description {
  margin: 0 0 12px;
}
.footer__image {
  margin: 0 0 13px;
}
.footer__store-image {
  max-width: 99px;
}
.footer__bottom {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 80px;
}
.footer__bottom::before {
  content: "";
  position: absolute;
  right: -500%;
  top: -50px;
  display: block;
  width: 1000%;
  height: 1px;
  background: var(--color-grey);
}
.footer__bottom-btn {
  border: 1px solid #241c15;
  padding: 9px;
}
.footer__bottom-text-wrapper {
  max-width: 300px;
  font-size: 10px;
}
.footer__social {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  width: 235px;
}
.footer__social-link {
  width: 20px;
  height: 20px;
}
.footer__social-image {
  width: 20px;
  height: 20px;
}
.footer__social-item:not(:last-child) {
  margin-right: 5px;
}

.category-item__title {
  margin: 0 0 30px;
  font-weight: 400;
  font-size: 1rem;
  line-height: 16px;
}

@media (max-width: 991.98px) {
  .swiper-slide {
    flex-direction: column;
  }
  .swiper__image {
    margin: 0;
    max-height: 400px;
    object-position: top;
  }
  .swiper__text {
    font-size: 24px;
    line-height: 1.3;
  }
  .swiper__text-wrapper {
    margin: 0 0 20px;
    padding: 0;
  }

  .swiper-pagination-bullets.swiper-pagination-horizontal {
    margin-bottom: -20px;
  }
}
@media (max-width: 799.9px) {
  .header__container {
    justify-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
  }
  .header__auth {
    justify-self: center;
  }

  .path__item {
    flex-direction: column;
  }
  .path__item-text-wrapper {
    padding-left: 20px;
  }

  .swiper {
    height: auto;
  }
  .swiper__image {
    margin: 0;
    max-height: 400px;
    object-position: top;
  }
  .swiper__text {
    font-size: 14px;
    line-height: 1.3;
  }

  .footer__bottom {
    padding: 0 30px;
  }
}
@media (max-width: 575.98px) {
  .hero__main-title {
    font-size: 52px;
  }

  .users__container {
    flex-direction: column;
  }
  .users__image {
    margin-bottom: 20px;
  }

  .swiper__image {
    margin: 0;
    max-height: 400px;
    object-position: top;
  }
  .swiper__text {
    font-size: 14px;
    line-height: 1.3;
  }

  .footer__social {
    margin-bottom: 20px;
  }
  .footer__top {
    grid-template-columns: none;
  }
  .footer__bottom {
    flex-direction: column;
    align-items: center;
  }
  .footer__bottom-store {
    margin-bottom: 20px;
  }
  .footer__bottom-text-wrapper {
    display: flex;
    flex-direction: column;
  }
  .footer__services {
    margin-right: 10px;
  }
}